.count-enter-active,.count-leave-active{
	-webkit-transition: all 0.3s linear;
	transition: all 0.3s linear;
}
.count-enter,.count-leave-to{
	opacity: 0;
	-webkit-transform: translate3d(0.48rem,0,0);
	        transform: translate3d(0.48rem,0,0);
}

.showfoodpage-enter-active,.showfoodpage-leave-active{
	-webkit-transition: all 0.3s linear;
	transition: all 0.3s linear;
	-webkit-transform: translate3d(0,0,0);
	        transform: translate3d(0,0,0);
}
.showfoodpage-enter,.showfoodpage-leave-to{
	-webkit-transform: translate3d(100%,0,0);
	        transform: translate3d(100%,0,0);
}
